* {
  margin: 0;
  padding: 0;
}

.container {
  margin-left: 8%;
  width: 84%;
}

.App {
  text-align: center;
  background-color: #f7f7f7;
  overflow-x: hidden;
  overflow-y: hidden;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.long-word-break {
  overflow-wrap: break-word; /* This is the new name for the property */
  word-wrap: break-all;     /* This is the old name for the property */
}